* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Karla, sans-serif;
  text-decoration: none;
  list-style: none;
}

body {
  background: #131b23;
  margin: 2px 30px 0 20px;
}

header {
  padding: 2rem 0;
  position: fixed;
  top: 0;
  left: 0;
}

.checkout {
  padding: 0 70px;
}

.checkout__title {
  color: #f64d33;
  justify-content: center;
  margin-top: 120px;
  margin-bottom: 30px;
  font-size: 25px;
  display: flex;
}

.checkout__subtitle {
  color: #fff;
  margin-top: 50px;
  margin-bottom: 10px;
  font-size: 18px;
}

.checkout__totalBox {
  justify-content: flex-end;
  margin-left: 3px;
  display: flex;
}

.checkout__total {
  color: #fff;
  margin-bottom: 5px;
  margin-left: 3px;
  font-size: 20px;
  font-weight: 100;
}

.checkout__total--number {
  color: #fff;
  margin-left: 5px;
  font-size: 20px;
}

.shopping {
  background: #ffffff31;
  border-radius: 10px;
  justify-content: space-between;
  align-items: center;
  margin: 10px 0 10px 0;
  padding: 20px 35px 20px 20px;
  display: flex;
}

.shopping__image {
  width: 90px;
  margin: 0 5px 0 0;
}

.shopping__info {
  color: #fff;
  font-size: 14px;
}

.shopping__name {
  color: #fff;
  width: 250px;
  padding-left: 50px;
  font-size: 15px;
  font-weight: 100;
}

.shopping__counter {
  font-size: 14px;
}

.shopping__delete {
  color: #f64d33;
  text-align: center;
  width: 80px;
  height: 28px;
  cursor: pointer;
  background: #fff;
  border: none;
  border-radius: 5px;
  font-weight: 200;
  box-shadow: 0 2px 4px #0003;
}

.shopping__delete:hover {
  background: #d3d0d0;
}

.item {
  flex-flow: column wrap;
  margin-top: 15px;
  display: flex;
}

.data__subtitle {
  color: #fff;
}

.data__label {
  color: #fff;
  letter-spacing: 1px;
  font-size: 16px;
  font-weight: 300;
  display: flex;
}

.data__input {
  width: 500px;
  background-color: #fff;
  border: none;
  border-radius: 5px;
  justify-content: flex-start;
  margin-top: 7px;
  padding: 12px 0;
}

.check {
  justify-content: center;
  align-items: center;
  margin-top: 25px;
  display: flex;
}

.checkout__submit {
  color: #fff;
  text-align: center;
  width: 300px;
  height: 38px;
  cursor: pointer;
  background: #253f5d;
  border: none;
  border-radius: 5px;
  margin-bottom: 20px;
  font-weight: 200;
  box-shadow: 0 2px 4px #0003;
}

.checkout__submit:hover {
  background: #fa614a;
}

.character {
  justify-content: flex-end;
  display: flex;
  position: absolute;
  top: 265px;
  left: 670px;
}

